    margin: 0 auto;
}

.CoverImg {
    margin: 10vh auto;
    width: 100%;
    height: 60vh;
    position: relative;
    left: 0;
    padding: 5% 0;
}

.ActivityWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    
