    width: 400px;
    max-width: 400px;
    
}

.ActivityImg {
    width: 80%;
    height: 320px;
    margin:  0 auto;
    border-radius: 8px;
    -webkit-box-shadow: 0 12px 24px -8px #777;
         box-shadow: 0 12px 24px -8px #777;
        }

.FavItem {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: "Image Fav"
        "Image Ans";
    max-width: 200px;
}

.Activity h2 {
    padding: 0;
    margin: 12px 0 0 0;
}


