.Wrapper {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0vh 100vh 100vh 100vh auto auto auto;
  position: absolute;
  top: 80px;
  left: 0;
  width: 100%;
  overflow: hidden;
}

.Blogs {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas: "Main"
    "Blog1"
    "Blog2"
    "Blog3"
    "Blog4";
  padding: 20px 0;
  margin: 0px auto;
  grid-column-gap: 32px;
}

.Footer {
  height: 100px;
}

