@-webkit-keyframes float {
  0% {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  50% {
    -webkit-transform: translatey(-10px);
            transform: translatey(-10px);
  }
  100% {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}

@keyframes float {
  0% {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  50% {
    -webkit-transform: translatey(-10px);
            transform: translatey(-10px);
  }
  100% {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}

.Wrapper {
  height: calc(100%);
  position: relative;
  overflow: hidden;
  margin: 0;
  left: 0;
  top: 140px;
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0% 60% 40%;
  grid-template-areas:
    "sideBar"
    "image"
    "card";
  background-color: #ffffff;
}

.Circle {
  width: 80vw;
  height: 80vw;
  border-radius: 50%;
  grid-area: image;
  justify-self: center;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
}

.Image {
  grid-area: image;
  width: 80vw;
  height: 100%;
  -ms-flex-item-align: center;
      align-self: center;
  justify-self: center;
  z-index: 0;
  -webkit-transform: translatey(0px);
      -ms-transform: translatey(0px);
          transform: translatey(0px);
  -webkit-animation: float 4s ease-in-out infinite;
          animation: float 4s ease-in-out infinite;
  background-size: contain;
  background-position: center;
}

@media (min-width: 720px) {
  .Wrapper {
    height: calc(100% - 80px);
    position: relative;
    overflow: hidden;
    margin: 0;
    left: 0;
    top: 80;
    width: 100vw;
    display: grid;
    grid-template-columns: 2fr 2fr;
    grid-template-rows: 1fr;
    grid-template-areas:
      " card image";

  }

  .Circle {
    position: relative;
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
    width: 40vw;
    height: 40vw;
  }

  .Image {
    width: 100%;
    height: 100%;
    position: relative;
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
  }
}



