.Wrapper {
  height: 80px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1000;
  width: 100%;
  display: grid;
  grid-auto-rows: 1fr;
  grid-auto-columns: 1fr 1fr;
  grid-template-areas: " one";
  background-color: #ffffff;
}

.One {
  height: 80px;
  width: 100%;
  justify-items: right;
  -ms-flex-align: center;
      align-items: center;
  display: grid;
  grid-area: one;
  grid-auto-rows: 1fr;
  grid-auto-columns: 2fr 1fr;
  grid-template-areas: " heading hbm";
}

.FrontMenu {
  display: none;
}

@media (min-width: 720px) {
  .One {
    grid-auto-columns: 2fr 4fr;
    grid-template-areas: " heading  FrontMenu";
  }

  .FrontMenu {
    grid-area: FrontMenu;
    display: grid;
    width: 80%;
    padding-right: 4vw;
    font-weight: 500;
    color: #555555;
    justify-items: flex-end;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
  }
}



