:root {
  --paddinng: 16px;
}

.Wrapper {
  width: calc(100vw - 16px);
  height: calc(40vh - 16px);
  background-color: "#efefef";
  position: relative;
  top: 0;
  left: 0;
  grid-area: card;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 1fr 1fr;
  grid-template-areas:
    " Main"
    " Body"
    "btn";
}

.Link {
  -ms-flex-item-align: start;
      align-self: flex-start;
  justify-self: center;
}

