@media (min-width: 720px) {
  .Wrapper {
    width: 100%;
    height: 50%;
    background-color: "#efefef";
    position: relative;
    align-self: center;
    justify-self: center;
    grid-area: card;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr 1fr 2fr;
    grid-template-areas:
      "Main"
      "Body"
      "btn";
  }
  .Link {
    -ms-flex-item-align: start;
        align-self: flex-start;
    justify-self: flex-start;
    padding-left: 10vw;
  }
}



