.Wrapper {
    height: 120px;
    width: calc(100% - 8vw);
    padding: 0 4vw;
    left: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows:  1fr 1fr;
    grid-template-areas: "h2 h2 h2"
    "one two three";
    -ms-flex-align: center;
        align-items: center;
    justify-items: center;
}

.Wrapper h2 {
    text-align: left;
    justify-self: flex-start;
    
}

.Wrapper a {
    text-decoration: none;
    color: #000000;
    justify-self: center;
}

.Wrapper a :hover {
    color: #000000;
}

@media (min-width: 720px) {
    .Wrapper {
        height: 120px;
        width: calc(100% - 8vw);
        padding: 0 4vw;
        left: 0;
        display: grid;
        grid-template-columns:  9fr 1fr 1fr 1fr;
        grid-template-rows:  1fr;
        grid-template-areas: "h2 one two three";
        -ms-flex-align: center;
            align-items: center;
        justify-items: center;
    }

    .Wrapper a {
        text-decoration: none;
        color: #000000;
        justify-self: flex-end;
    }

    .Wrapper a :hover {
        color: aqua;
    }

}


